import React, { Component } from 'react';
import BaseVisualization from '@splunk/dashboard-visualizations/common/BaseVisualization';
import { truncateParsedData } from '@splunk/dashboard-visualizations/utils/dataUtils';
import {
    withVisualizationAndThemeHOCs,
    withPaginator,
} from '@splunk/dashboard-visualizations/utils/enhancer';
import ReactBar from '@splunk/react-visualizations/Bar';
import { parse, dataContract } from '@splunk/visualization-encoding-parsers/BarParser';
import { enterprise } from '@splunk/visualization-themes/variables';

import VisualizationEvent from '@splunk/dashboard-visualizations/common/VisualizationEvent';
import IconPlaceholder from '@splunk/dashboard-visualizations/common/IconPlaceholder';
import { Bar as BarIcon } from '@splunk/visualization-icons';
import { getPropsForSimpleXMLOptions } from '@splunk/visualizations-shared/simpleXMLOptions';
import { getChartingThemeKey } from '../../utils/themeUtils';
import { enableClickHandler } from '../../utils/eventUtils';
import optionsSchema from './optionsSchema';
import editorConfig from './editorConfig';

const BAR_CHART_PAGINATION_HEIGHT_OFFSET = 30;

export class BarComponent extends Component {
    static propTypes = BaseVisualization.propTypes;

    static defaultProps = {
        ...BaseVisualization.defaultProps,
        height: 250,
    };

    static dataContract = dataContract;

    static vizContract = {
        initialDimension: {
            width: 300,
            height: 300,
        },
    };

    static schema = optionsSchema;

    static editor = editorConfig;

    render() {
        const {
            dataSources,
            encoding,
            options,
            width,
            height,
            style,
            hasEventHandlers,
            theme,
            mode,
        } = this.props;
        const themeKey = getChartingThemeKey(theme);
        let barProps = parse(dataSources, encoding);
        // todo: may need merge the truncateParsedData function with parse
        const truncationLimit = options && options['chart.resultTruncationLimit'];
        barProps = truncateParsedData(barProps, truncationLimit);

        const onPointClick = evt => {
            if (enableClickHandler(hasEventHandlers, mode)) {
                this.props.onEventTrigger(
                    new VisualizationEvent({
                        originalEvent: evt.originalEvent,
                        payload: evt.payload,
                        type: 'point.click',
                    })
                );
            }
        };

        const onLegendClick = evt => {
            if (enableClickHandler(hasEventHandlers, mode)) {
                this.props.onEventTrigger(
                    new VisualizationEvent({
                        originalEvent: evt.originalEvent,
                        payload: evt.payload,
                        type: 'legend.click',
                    })
                );
            }
        };

        return (
            <ReactBar
                width={width}
                height={height}
                style={style}
                themeKey={themeKey}
                x={barProps.x}
                y={barProps.y}
                y2={barProps.y2}
                xFieldName={barProps.xFieldName}
                yFieldName={barProps.yFieldName}
                y2FieldName={barProps.y2FieldName}
                onPointClick={onPointClick}
                onLegendClick={onLegendClick}
                {...getPropsForSimpleXMLOptions(options)}
            />
        );
    }
}

export default withVisualizationAndThemeHOCs({
    defaultBackgroundColor: enterprise.defaultBackgroundColor,
    placeholder: <IconPlaceholder icon={<BarIcon />} />,
})(
    withPaginator({
        VizHeightOffset: BAR_CHART_PAGINATION_HEIGHT_OFFSET,
    })(BarComponent)
);
